*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  scroll-behavior: smooth;
}

body {
  box-sizing: border-box;
  overflow-x: hidden;
  scroll-behavior: smooth;
}

a {
  display: inline-block;
  text-decoration: none;
}

ul, ol {
  list-style: none;
}

button {
  cursor: pointer;
  border: none;
  background-color: none;
}

input {
  display: inline-block;
  border: none;
}

img {
  display: inline-block;
  max-width: 100%;
}

/*
  FONT SIZES
  75px | 56px | 42px | 32px | 24px | 17px | 13px | 10px | 7.5px | 5.6px | 4.2px
*/
html {
  font-size: 50%;
}
@media (min-width: 75em) {
  html {
    font-size: 56.25%;
  }
}
@media (min-width: 112.5em) {
  html {
    font-size: 62.5%;
  }
}

body {
  font-size: 1.6rem;
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  line-height: 1.7;
  color: #6E7A89;
}

.heading-primary {
  font-size: 4.2rem;
  color: #FFFFFF;
}
@media (min-width: 56.25em) {
  .heading-primary {
    width: 85%;
    font-size: 5.6rem;
  }
}
@media (min-width: 75em) {
  .heading-primary {
    line-height: 1.5;
    font-size: 6.2rem;
  }
}

.heading-secondary {
  margin: 0 auto;
  margin-bottom: 1.6rem;
  width: 85%;
  font-size: 4rem;
  font-weight: 600;
  color: #132742;
  text-align: center;
  line-height: 1.2;
}
@media (min-width: 56.25em) {
  .heading-secondary {
    text-align: left;
    line-height: 1.5;
    width: 100%;
  }
}

.heading-tertiary {
  font-size: 2.4rem;
  color: #132742;
  margin-bottom: 1.2rem;
}

.u-display-none {
  display: none;
}

.u-text-center {
  text-align: center;
}

.u-margin-big {
  margin-bottom: 4.8rem;
}

.u-color-black {
  color: #000000;
}

.u-width-20rem {
  width: 20rem;
}

.u-width-100 {
  width: 100%;
}